import React, { useEffect, useState } from "react";
import Nav from "../common/nav";
import Footer from "../common/footer";
import _ from "lodash";
import CircleGraph from "../common/circleGraph";
import Button from "../common/Button";
import Input from "../common/form-input";
import AdjustableInput from "../common/form-input-adjustable";
import Textarea from "../common/form-textarea";

const Envelopes = ({ id }) => {
  const [envelope, setEnvelope] = useState({});
  const [envelopes, setEnvelopes] = useState([
    {
      id: 1,
      name: "Emergency Savings",
      account: "HY Savings",
      desc: "Buffer fund",
      tag: { name: "Savings", color: "warning" },
      values: {
        goal: 12687,
        amount: 10724.73,
        percentage: 85,
      },
    },
    {
      id: 2,
      name: "Dining Out",
      account: "General Spending",
      desc: "Money for eating out",
      tag: { name: "Food", color: "success" },
      values: {
        goal: null,
        amount: 120,
      },
    },
    {
      id: 3,
      name: "Groceries",
      account: "General Spending",
      desc: "Money for grocery spending",
      tag: { name: "Food", color: "success" },
      values: {
        goal: null,
        amount: 280.19,
      },
    },
    {
      id: 4,
      name: "Kids",
      account: "General Spending",
      desc: "Kid related purchases",
      tag: { name: "Kids", color: "info" },
      values: {
        goal: null,
        amount: 30,
      },
    },
  ]);

  const [showPosAdjuster, togglePosAdjuster] = useState(false);
  const [showNegAdjuster, toggleNegAdjuster] = useState(false);
  const [addTotal, setAddTotal] = useState("");
  const [subTotal, setSubTotal] = useState("");

  useEffect(() => {
    if (id) {
      console.log(typeof id);
      setEnvelope(envelopes.find((value) => value.id === Number(id)));
    }
  }, [id, envelopes]);

  const adjuster = (type = null) => {
    togglePosAdjuster(false);
    toggleNegAdjuster(false);
    if (type !== null) {
      type ? togglePosAdjuster(true) : toggleNegAdjuster(true);
    }
  };

  const updateEnvelope = (e) => {
    console.log(e);
  };

  const add2total = () => {
    let update = envelope;

    update.values.amount = update.values.amount + Number(addTotal);
    if (update.values.goal) {
      update.values.percentage = Math.round(
        (update.values.amount / update.values.goal) * 100
      );
    }
    setEnvelope({ ...envelope, ...update });
  };

  const subFromTotal = () => {
    let update = envelope;
    update.values.amount = update.values.amount - Number(subTotal);
    if (update.values.goal) {
      update.values.percentage = Math.round(
        (update.values.amount / update.values.goal) * 100
      );
    }
    setEnvelope({ ...envelope, ...update });
  };

  return (
    <>
      <Nav />
      <section>
        {!_.isEmpty(envelope) && (
          <div className="container envelope">
            <h1>{envelope.name}</h1>
            <hr />
            <div className="flex-wrapper start mt-5">
              <div className="me-5 fg-1">
                <CircleGraph props={envelope.values} showPercent={true} />
              </div>
              <form className="fg-3">
                <div className="flex-wrapper">
                  <h4 className="tag mb-4">
                    <span className={"badge bg-" + envelope.tag.color}>
                      {envelope.tag.name}
                    </span>
                  </h4>
                  <Button className="btn-light btn-edit">
                    Edit envelope <span className="edit"></span>
                  </Button>
                </div>
                <h5>Description</h5>
                <Textarea
                  disabled={true}
                  value={envelope.desc && envelope.desc}
                  placeholder={envelope.desc && envelope.desc}
                  name="desc"
                  onChange={(e) => updateEnvelope(e)}
                />
                <h5>Amounts</h5>
                <div className="flex-wrapper m-space">
                  <Input
                    disabled={true}
                    value={envelope.values && envelope.values.goal}
                    placeholder={envelope.values && envelope.values.goal}
                    onChange={(e) => updateEnvelope(e)}
                    className="mw-150"
                    label="Goal"
                    name="goal"
                    help="Desired goal to reach."
                  />
                  <div className="flex column">
                    <AdjustableInput
                      disabled={true}
                      value={envelope.values && envelope.values.amount}
                      placeholder={envelope.values && envelope.values.amount}
                      adjustableClick={(v) => adjuster(v)}
                      onChange={(e) => updateEnvelope(e)}
                      className="mw-150"
                      label="Total"
                      name="total"
                      help="Current amount in envelope."
                    />
                    {showPosAdjuster && (
                      <div className="input-button-wrapper">
                        <Input
                          help="Enter amount to add."
                          className="mw-150"
                          name="add-total"
                          value={addTotal}
                          onChange={(e) => setAddTotal(e)}
                        />
                        <Button
                          text="Add"
                          className="btn-success"
                          action={() => add2total()}
                        />
                        <Button
                          className="btn-close"
                          action={() => adjuster()}
                        />
                      </div>
                    )}
                    {showNegAdjuster && (
                      <div className="input-button-wrapper">
                        <Input
                          help="Enter amount to subtract."
                          className="mw-150"
                          name="sub-total"
                          value={subTotal}
                          onChange={(e) => setSubTotal(e)}
                        />
                        <Button
                          text="Subtract"
                          className="btn-warning"
                          action={() => subFromTotal()}
                        />
                        <Button
                          className="btn-close"
                          action={() => adjuster()}
                        />
                      </div>
                    )}
                  </div>
                </div>
              </form>
            </div>
          </div>
        )}
      </section>
      <Footer />
    </>
  );
};

export default Envelopes;
